import { Box, HStack, Heading, Image, Stack, Text, useColorModeValue } from "@chakra-ui/react"
import profile from "../assets/profile.jpg"
import bg1 from "../assets/bg1.gif"
import { m } from "framer-motion"
export const Home = () => {
    return (
        <Box bg={useColorModeValue('blue.100', 'black.100')}  className="home" id="home" >
            <Box py={["4rem","5rem"]}>
            <Text textAlign="center" fontWeight={"semibold"} fontSize={"20px"}>Welcome To My Portfolio</Text>
            <Stack w={"100%"}   justifyContent={"space-between"} py={["1rem","2rem"]} px={["1rem","2rem","3rem","5rem"]} direction={["column","column","row","row"]}  >
                <Stack textAlign={["center","center","left"]} spacing={2}>
                 <Text fontSize={"2xl"}>Hi, 👋 I'm</Text>
                 <Heading  fontStyle={"revert-layer"} fontFamily={""}>Bhupendra Kumar Chandrakar</Heading>
                 <Text fontSize={"2xl"} fontFamily={"sans-serif"} >Full Stack Web Developer</Text>
                </Stack>
                
                 <Image alignSelf={"center"} m={"auto"} w={["150px","200px","250px"]} h={["150px","200px","250px"]} rounded={"100px"} src={profile}/>
                
            </Stack>  
            </Box>
        </Box>
    )
}